*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 24px; } }
  @media (max-width: 768px) {
    .container {
      padding: 0 35px;
      position: fixed;
      left: 0;
      top: 74px;
      right: 0;
      bottom: 0;
      width: 100%; } }
  @media (max-width: 768px) {
    .container--no-padding {
      padding: 0; } }

.container-none {
  display: none; }

.container__block {
  display: none;
  flex-direction: row;
  animation-duration: 3s;
  animation-name: slideIn;
  animation-iteration-count: 1;
  animation-direction: 3s; }

.container__dragon {
  display: block; }
  @media (max-width: 1023px) {
    .container__dragon {
      display: none; } }

.container__slogan {
  max-width: 560px;
  font-size: 96px;
  line-height: 96px;
  color: #2c39f2;
  margin-top: 108px; }
  @media (max-width: 1023px) {
    .container__slogan {
      max-width: 640px; } }

@keyframes slideIn {
  from {
    margin-left: 100%;
    width: 300%; }
  to {
    margin-left: 0;
    width: 100%; } }

.body__scroll--hidden {
  overflow: hidden; }

.header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
  clear: both; }
  @media (max-width: 1439px) {
    .header {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .header {
      padding: 0 24px;
      z-index: 99; } }
  @media (max-width: 768px) {
    .header {
      padding: 0 35px; } }

.header__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  height: 74px;
  max-width: 1024px;
  padding: 0 39px; }
  @media (max-width: 1023px) {
    .header__wrapper {
      flex-wrap: wrap;
      padding: 0; } }

.header__logo {
  width: 192px;
  height: 24px;
  margin-top: 25px; }

.header__logo--svg {
  width: 192px;
  height: 24px; }

@media (max-width: 1023px) {
  .header__nav {
    display: none; } }

.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #ffffff;
  z-index: 1001; }

.loader--none {
  display: none; }

.loader__logo {
  width: 800px;
  height: 100px;
  padding: 0 20px;
  animation: loader__logo 1.5s linear normal 1; }
  @media (max-width: 768px) {
    .loader__logo {
      width: 550px;
      height: 70px;
      padding: 0; } }

@keyframes loader__logo {
  90% {
    transform: scale(0.5); }
  100% {
    transform: scale(1.1); }
  90% {
    transform: scale(0.5); } }

.loader__hint {
  position: absolute;
  width: 216px;
  height: 22px;
  bottom: 10%;
  font-family: Neue Machina;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 22px;
  color: #000000; }
  @media (max-width: 768px) {
    .loader__hint {
      display: none; } }

.loader__hint--tablet {
  display: none; }
  @media (max-width: 768px) {
    .loader__hint--tablet {
      display: block;
      position: absolute;
      width: 228px;
      height: 22px;
      bottom: 9.76%;
